console.log('当前环境：' + process.env.NODE_ENV);

let api_config = {};

if (process.env.NODE_ENV === 'production') {
  api_config = {
    baseUrl: '/demo/static/data/'
  }
} else {
  api_config = {
    baseUrl: '/static/data/'
  };
}

export default api_config;



